:root {
  --font-body: "Lato", sans-serif;
  --font-stat: "Stalemate", cursive;
}

:root {
  --white: hsl(0, 0%, 100%);
  --whiteLight: hsl(0, 8%, 75%);
  --black: hsl(0, 0%, 0%);
  --violet: hsl(203, 84%, 17%);
  --gray:hsl(0, 0%, 53%);
  --grayLight:hsla(0, 0%, 91%, 1);
  --grayDark: hsla(0, 0%, 89%, 1);
  --pink: hsla(7, 82%, 67%, 1);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-body);
}

h1,
h2,
h3 {
  margin: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

p {
  margin: 0;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.container {
  padding: 0 1.5rem;
  max-width: 69.375rem;
  margin: 0 auto;
}
@media (min-width: ) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

.containers, .conteneur {
  padding: 0 8.75rem;
  max-width: 87.5rem;
  margin: 0 auto;
}
@media (max-width: 59.375em) {
  .containers, .conteneur {
    padding: 0 3.125rem;
  }
}
@media (max-width: 39.9375em) {
  .containers, .conteneur {
    padding: 0 1.25rem;
  }
}

