.ladies_grid {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  justify-content: center;
  gap: 0.625rem;
}
@media (max-width: 39.9375em) {
  .ladies_grid {
    grid-template-columns: repeat(3, 1fr);
  }
}
.ladies_grid__container {
  height: 79vmin;
  max-height: 45vmax;
  overflow: hidden;
}
@media (min-width: 26.563em) {
  .ladies_grid__container {
    max-height: 80vw;
  }
}
@media (min-width: 40em) {
  .ladies_grid__container {
    max-height: 25vw;
  }
}
@media (min-width: 48.75em) {
  .ladies_grid__container {
    max-height: 30vw;
  }
}
@media (min-width: 59.375em) {
  .ladies_grid__container {
    max-height: 34vw;
  }
}
@media (min-width: 87.5em) {
  .ladies_grid__container {
    max-height: 38vw;
  }
}
@media (min-width: 160em) {
  .ladies_grid__container {
    max-height: 43vw;
  }
}
.ladies_grid__container .img-container {
  width: 100%;
  display: flex;
  flex-direction: column;
}
.ladies_grid__container .img-container img {
  width: 100%;
}
.ladies_grid__container .const1 {
  animation: slide 16.5s infinite;
}
.ladies_grid__container .const2 {
  animation: slide 16s infinite;
}
.ladies_grid__container .const3 {
  animation: slide 15.5s infinite;
}
.ladies_grid__container .const4 {
  animation: slide 15s infinite;
}
.ladies_grid__container .const5 {
  animation: slide 14.5s infinite;
}
.ladies_grid__container .const6 {
  animation: slide 14s infinite;
}

@keyframes slide {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(0);
  }
  55% {
    transform: translateY(-50%);
  }
  100% {
    transform: translateY(-50%);
  }
}
.programme {
  padding-top: 3rem;
}
.programme__bloc1 {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (max-width: 39.9375em) {
  .programme__bloc1 {
    grid-template-columns: 1fr;
  }
}
.programme__bloc1__slider {
  position: relative;
  align-self: center;
}
@media (max-width: 39.9375em) {
  .programme__bloc1__slider {
    display: none;
  }
}
.programme__bloc1__slider .slide {
  display: none;
  z-index: 1;
  overflow: hidden;
}
.programme__bloc1__slider .slide img {
  z-index: 1;
  width: 100%;
}
.programme__bloc1__slider .fades {
  -webkit-animation-name: fade;
  -webkit-animation-duration: 1.5s;
  animation-name: fade;
  animation-duration: 1.5s;
}
@-webkit-keyframes fade {
  from {
    opacity: 0.4;
    transform: translateX(-2000px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes fade {
  from {
    opacity: 0.4;
    transform: translateX(-2000px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
.programme__bloc1__slider .navigation {
  z-index: 2;
  display: none;
  position: absolute;
  display: flex;
  bottom: 1.875rem;
  left: 50%;
  transform: translateX(-50%);
}
.programme__bloc1__slider .navigation .manualbtn {
  display: none;
  background: #d9d9d9;
  width: 0.75rem;
  height: 0.75rem;
  margin: 0.625rem;
  border-radius: 50%;
  cursor: pointer;
}
.programme__bloc1__slider .navigation .manualbtn.active {
  background: #f07667;
  box-shadow: 0 0 0.125rem rgba(0, 0, 0, 0.5);
}
.programme__bloc1__text {
  display: grid;
  grid-template-columns: 1fr;
  gap: 2rem;
  height: 80vh;
  overflow-y: scroll;
  padding: 1rem 0.5rem 0 0;
  align-self: center;
}
@media (max-width: 59.375em) {
  .programme__bloc1__text {
    height: 60vh;
  }
}
@media (max-width: 39.9375em) {
  .programme__bloc1__text {
    overflow-y: auto;
    height: auto;
  }
}
.programme__bloc1 ::-webkit-scrollbar {
  width: 0.2rem;
  background-color: rgba(128, 128, 128, 0.411);
  border-radius: 0.5rem;
  cursor: pointer;
}
.programme__bloc1 ::-webkit-scrollbar-thumb {
  background-color: #f07667;
  border-radius: 0.5rem;
  cursor: pointer;
}
.programme__bloc1__content {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.programme__bloc1__date {
  display: flex;
  gap: 1rem;
}
@media (max-width: 26.563em) {
  .programme__bloc1__date {
    gap: 0.3rem;
  }
}
.programme__bloc1__date :nth-child(2) {
  width: 30%;
  height: 0.05rem;
  background-color: var(--black);
  align-self: center;
  margin-top: 0.5rem;
}
.programme__bloc1__activity {
  display: grid;
  grid-template-columns: 0.5fr 1fr;
}
@media (max-width: 26.563em) {
  .programme__bloc1__activity {
    grid-template-columns: 0.9fr 1fr;
  }
}
.programme__bloc1__event p {
  padding-bottom: 0.8rem;
}
.programme__bloc1__text__slider {
  display: none;
}
@media (max-width: 39.9375em) {
  .programme__bloc1__text__slider {
    display: block;
  }
}
.programme__bloc1__text__slider .slide {
  display: none;
}
.programme__bloc1__text__slider .slide img {
  width: 100%;
}

.programme__paragraph,
.programme__paragraph--bold {
  font-family: "Lato";
  font-weight: 400;
  font-size: 0.8125rem;
  text-align: justify;
}

.programme__paragraph--bold {
  font-weight: 700;
}

.programme__title {
  font-family: "Stalemate";
  font-style: normal;
  font-weight: 400;
  font-size: 4rem;
  text-align: center;
  color: --black;
}